@import "mixins";

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: flex-end;
  min-height: 60px;
  padding: 9px 0;
  color: white;
  background-color: cca(#093465, .6);

  &__user {
    display: flex;
    align-items: center;
    border-left: 1px solid #0b5193;
    padding: 0 18px;
  }

  &__avatar {
    margin-right: 14px;
    background-color: cca(white, .8);
  }

  &__amount {
    align-self: center;
    padding: 0 18px;

    &::before {
      @extend %icon-redblue-tron;
    }
  }
}