*,
*::before,
*::after {
    box-sizing: border-box;
}

blockquote, body, code, dd, div, dl, dt, fieldset, form, h1, h2, h3, h4, h5, h6, input, li, p, pre, td, textarea, th, ul, button {
    margin: 0;
    padding: 0;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

abbr, fieldset, img, button {
    border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
    font-style: normal;
    font-weight: 400;
}

ul li {
    list-style: none;
}

caption, th {
    text-align: left;
}

h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
    font-weight: 400;
}

sup {
    vertical-align: text-top;
}

sub {
    vertical-align: text-bottom;
}

input, select, textarea {
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    outline: none;
    border: none;
}

legend {
    color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section {
    display: block;
}

img {
    max-width: 100%;
    height: auto;
}

button {
    font-family: inherit;
    outline: none;
    background: none;
}

a {
    color: inherit;
    text-decoration: none;
}
