@import "reset";
@import "mixins";
@import "breaks";
@import "avatars";
@import "icomoon";
@import "header";

$border-radius-redblue: 4px;
$border-radius-redblue-2: 10px;

$color-redblue-blue-2: #00244c;
$color-redblue-blue-1: #002e60;
$color-redblue-blue-5: #26507d;
$color-redblue-blue-3: #8db2da;
$color-redblue-blue-4: #3d5afe;
$color-redblue-red-1: #ff1744;

$filter-hover: saturate(0) brightness(3);

%icon-redblue-tron {
  @extend %im;
  content: $icomoon-tron;
  font-size: 13px;
  margin-right: 5px;
}

body {
  background: #0a335f url("assets/images/new-game-background.jpg") top center no-repeat;
  background-size: cover;
  font-family: sans-serif;

  header {
    z-index: 2;
  }
}

.red-blue__wrapper {
  $width: 1170px;
  $width-2: 570px;
  $padding: 100px;
  $padding-2: 20px;

  max-width: $width + $padding * 2;
  min-height: 100vh;
  padding: $padding;
  padding-bottom: 30px;
  margin: 0 auto;
  box-sizing: border-box;

  @media screen and (max-width: 1000px) {
    max-width: $width-2 + $padding-2 * 2;
    padding: 50px $padding-2;
  }

  &--overflow-hidden {
    overflow: hidden;
  }

  button {
    font-family: "SF UI Display", sans-serif;
  }
}

.redblue-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000b18;
  opacity: .8;
  //transition: opacity 1s;
  z-index: 1;
  //pointer-events: none;
}

$break-redblue: 1300px;
$break-redblue-2: 1000px;

.columner {
  $margin: 15px;
  $margin-2: 10px;

  $break: $break-redblue;
  $break-2: $break-redblue-2;

  display: flex;
  flex-wrap: wrap;
  margin-left: -$margin;
  margin-right: -$margin;

  @media screen and (max-width: $break) {
    margin-left: -$margin-2;
    margin-right: -$margin-2;
  }

  &--margin {
    @include fluid(margin-top, 1300px, 1000px, 86px, 50px, true, false);

    @media screen and (max-width: 1000px) {
      margin-top: 30px;
    }
  }

  &__column {
    margin: 0 $margin;

    @media screen and (max-width: $break) {
      margin: 0 $margin-2;
    }

    &--1 {
      width: calc(#{100% / 2} - #{$margin * 2});

      @media screen and (max-width: $break) {
        width: calc(#{100% / 2} - #{$margin-2 * 2});
      }
      @media screen and (max-width: $break-2) {
        width: calc(100% - #{$margin-2 * 2});
        &:not(:first-child) {
          margin-top: 30px;

          @media screen and (max-width: 500px) {
            margin-top: 20px;
          }
        }
      }
    }

    &--2 {
      width: calc(#{100% / 3} - #{$margin * 2});

      @media screen and (max-width: $break) {
        width: calc(#{100% / 3} - #{$margin-2 * 2});
      }
    }
  }
}

.button-rb {
  position: relative;
  display: block;
  color: white;
  border-radius: $border-radius-redblue;
  user-select: none;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    border-radius: $border-radius-redblue;
    transition: opacity .2s;
    will-change: opacity;
  }

  &:not([disabled]) {
    cursor: pointer;
  }

  &[disabled],
  &:hover {
    &::before {
      opacity: 0;
    }
  }

  &__label {
    position: relative;
  }

  &--red {
    background-color: lighten($color-redblue-red-1, 10%);

    &::before {
      background-color: $color-redblue-red-1;
    }
  }

  &--blue {
    background-color: lighten($color-redblue-blue-4, 10%);

    &::before {
      background-color: $color-redblue-blue-4;
    }
  }

  &__br {
    display: none;

    @media screen and (max-width: 1300px) {
      display: inline;
    }
    @media screen and (max-width: 1000px) {
      display: none;
    }
    @media screen and (max-width: 660px) {
      display: inline;
    }
  }
}

.column-game {
  display: flex;

  &__column-last-wins {
    margin-right: 30px;

    @media screen and (max-width: 1300px) {
      margin-right: 20px;
    }
    @media screen and (max-width: 500px) {
      display: none;
    }
  }

  &__column-wheel {
    margin: 0 auto;
  }
}

.last-wins {
  color: white;

  &__header {
    //display: flex;
    //align-items: center;
    text-transform: uppercase;
    margin-bottom: 22px;

    @media screen and (max-width: 1090px) {
      font-size: 14px;
    }

    &::before {
      @extend %im;
      content: $icomoon-dividends;
      font-size: 13px;
      margin-right: 8px;
    }
  }

  &__list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 104px;
    border-left: 3px solid;
    border-radius: $border-radius-redblue;
    padding: 4px 13px;
    margin-top: 8px;
    box-sizing: border-box;
    font-size: 14px;
    text-align: right;
    background: $color-redblue-blue-1;

    @media screen and (max-width: 1090px) {
      min-width: 94px;
      padding: 3px 8px;
      margin-top: 7px;
      font-size: 12px;
    }

    &::before {
      @extend %icon-redblue-tron;
    }

    &--red {
      border-color: $color-redblue-red-1;
    }

    &--blue {
      border-color: $color-redblue-blue-4;
    }
  }
}

.anim-last-wins {
  &-leave,
  &-enter {
    &-active {
      transition: none 1s;
      transition-property: transform, opacity;
    }
  }

  &-leave-active {
    position: absolute;
  }

  &-enter {
    transform: translateY(-100%);
    opacity: 0;
  }

  &-leave-to {
    opacity: 0;
  }

  &-move {
    transition: none 1s;
    transition-property: transform, opacity;
  }
}

@keyframes animate-test {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


@keyframes animate-redblue-glow {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}


@keyframes animate-redblue-glow-inner {
  0% {
    transform: translate(-50%, -50%) rotate(25deg) scale(0.7);
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) rotate(25deg) scale(1.4);
    opacity: 0;
  }
}


.wheel {
  position: relative;
  max-width: 330px;

  /*svg {
    animation: animate-test 30s infinite linear
  }*/

  @media screen and (max-width: 1000px) {
    max-width: 290px;
  }

  &__blue {
    fill: $color-redblue-blue-4;
  }

  &__red {
    fill: $color-redblue-red-1;
  }

  &__mask {
    stroke-width: 20;
  }

  &__cursor-container {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 100%;
    display: flex;
    justify-content: center;
  }

  &__cursor {
    fill: white;
  }

  &__timer-container,
  &__glow {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &__timer-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__timer {
    position: relative;
    z-index: 2;
    font-size: 36px;
    text-align: center;
    color: white;
  }

  &__glow::before,
  &__glow::after,
  &__glow-inner::before {
    content: url(data:image/png;base64,);
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: .7;
  }

  &__glow {
    z-index: 1;

    &::before, &::after {
      animation: animate-redblue-glow 30s infinite linear;
    }

    &::after {
      animation-direction: reverse;
    }

    &--blue {
      filter: hue-rotate(250deg);
    }
  }

  &__glow-inner {
    &::before {
      animation: animate-redblue-glow-inner 1.5s ease-out;
      animation-fill-mode: both;
    }
  }

  &__wheel-container {
    overflow: hidden;
  }
}

.anim-wheel-timer {
  &-leave,
  &-enter {
    &-active {
      transition: none .5s;
      transition-property: transform, opacity;
    }
  }

  &-enter,
  &-leave-to {
    transform: scale(0.9);
    opacity: 0;
  }
}

.anim-wheel-cursor {
  &-leave,
  &-enter {
    &-active {
      transition: none .5s;
      transition-property: opacity;
    }
  }

  &-enter,
  &-leave-to {
    opacity: 0;
  }
}

.anim-wheel-glow {
  &-leave,
  &-enter {
    &-active {
      transition: none 1s;
      transition-property: opacity;
    }
  }

  &-enter,
  &-leave-to {
    opacity: 0;
  }
}







$padding-redblue-blick-blue: 30px;
$padding-redblue-blick-blue-2: 20px;
$padding-redblue-blick-blue-3: 14px;

.block-blue {
  border-radius: $border-radius-redblue-2;
  border-bottom: 2px solid $color-redblue-blue-2;
  @include fluid(padding, 1300px, 1000px, $padding-redblue-blick-blue, $padding-redblue-blick-blue-2);
  background: $color-redblue-blue-1;

  @media screen and (max-width: 500px) {
    padding: $padding-redblue-blick-blue-3;
  }

  &:not(:first-child) {
    margin-top: 27px;

    @media screen and (max-width: 500px) {
      margin-top: 18px;
    }
  }
}





.at-stake {
  display: flex;
  justify-content: space-between;

  &__header,
  &__content {
    text-align: center;

    &--left {
      text-align: left;
    }

    &--right {
      text-align: right;
    }
  }

  &__header {
    margin-top: 3px;
    margin-bottom: 8px;
    font-size: 14px;
    color: $color-redblue-blue-3;

    &::before {
      @extend %icon-redblue-tron;
    }

    &--left,
    &--right {
      &::before {
        content: $icomoon-user;
      }
    }
  }

  &__content {
    font-size: 36px;
    @include fluid(font-size, 500px, 320px, 36px, 24px, false, false);
    color: white;

    &--red {
      color: $color-redblue-red-1;
    }

    &--blue {
      color: $color-redblue-blue-4;
    }
  }

  &__side {
    min-width: 88px;
    @include fluid(min-width, 500px, 320px, 88px, 58px, false, false);
  }
}

.bet-editor {
  &__column-item {
    min-height: 60px;
    border-radius: $border-radius-redblue;
    font-size: 18px;
    text-transform: uppercase;
    color: white;

    @media screen and (max-width: 500px) {
      min-height: 54px;
      font-size: 16px;
    }
  }

  &__input {
    text-align: center;
    background: $color-redblue-blue-2;
  }

  &__text {
    margin-top: 24px;
    line-height: 1.2;
    color: $color-redblue-blue-3;

    @media screen and (max-width: 500px) {
      margin-top: 16px;
    }
  }
}

.bets {
  $padding-side: 30px;
  transition: none 1s;
  transition-property: filter, opacity;

  &--fade {
    filter: grayscale(1);
    opacity: .7;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    padding: 0 $padding-side;
    margin-bottom: 25px;
    color: white;
    transform: translateZ(0);

    @media screen and (max-width: 1000px) {
      margin: 0;
    }
  }

  &__header-users {
    display: flex;
    align-items: center;

    &::before {
      @extend %im;
      content: $icomoon-user;
      font-size: 19px;
      margin-right: 8px;
    }

    &--red {
      &::before {
        color: $color-redblue-red-1;
      }
    }

    &--blue {
      &::before {
        color: $color-redblue-blue-4;
      }
    }
  }

  &__header-bets {
    display: flex;
    align-items: flex-end;
    font-size: 14px;
  }

  &__header-label {
    display: inline-block;
    margin-right: 16px;
    color: $color-redblue-blue-3;
  }

  &__header-amount {
    &::before {
      @extend %icon-redblue-tron;
    }
  }

  &__list {
    border-top: 2px solid;
    border-radius: $border-radius-redblue-2;
    padding-top: 20px;
    transform: translateZ(0);

    @media screen and (max-width: 1000px) {
      display: none;
    }
  }

  &__list-bottom {
    border-bottom: 2px solid;
    border-radius: $border-radius-redblue-2;
    padding-bottom: 20px;
  }

  &__list,
  &__list-bottom {
    &--red {
      border-color: $color-redblue-red-1;
    }

    &--blue {
      border-color: $color-redblue-blue-4;
    }
  }

  &__list-item {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 9px $padding-side;
    font-size: 14px;
    color: $color-redblue-blue-3;

    &::before {
      content: "";
      position: absolute;
      left: $padding-side;
      right: $padding-side;
      bottom: 0;
      height: 1px;
      background: $color-redblue-blue-5;
      opacity: 0;
      transition: opacity .5s;
    }

    &:not(:nth-last-child(2)):not(&--red):not(&--blue) {
      &::before {
        opacity: 1;
      }
    }

    &--red,
    &--blue {
      border-radius: $border-radius-redblue-2;
    }

    &--red {
      background-image: linear-gradient(to top, $color-redblue-red-1, transparent 80%);
    }

    &--blue {
      background-image: linear-gradient(to top, $color-redblue-blue-4, transparent 80%);
    }
  }

  &__list-user {
    display: flex;
    align-items: center;
  }

  &__item-amount {
    &::before {
      @extend %icon-redblue-tron;
    }
  }

  &__avatar {
    margin-right: 14px;
    background-color: cca(white, .8);
  }
}

.anim-bet-list {
  &-leave,
  &-enter {
    &-active {
      transition: none .5s;
      transition-property: opacity;
    }
  }

  &-enter,
  &-leave-to {
    opacity: 0;
  }
}

.anim-bet-item {
  &-leave,
  &-enter {
    &-active {
      transition: none .5s;
      transition-property: transform, opacity;
    }
  }

  /*&-leave-active {
    position: absolute;
  }*/

  &-enter {
    transform: translateY(-50%);
    opacity: 0;
  }

  /*&-leave-to {
    transform: translateY(50%);
    opacity: 0;
  }*/

  &-move {
    transition: none .5s;
    transition-property: transform, opacity;
  }
}

.anim-overlay {
  &-leave,
  &-enter {
    &-active {
      transition: none .5s;
      transition-property: opacity;
    }
  }

  &-enter,
  &-leave-to {
    opacity: 0;
  }
}
