.avatar {
  display: block;
  flex-shrink: 0;
  border-radius: 5px;
  box-sizing: content-box;
  background-color: #f0f0f0;

  &-11 {
    @include customIcon2(5 * 11px, 6px, $isSize: false);
    @include customIcon2(5 * 10px, 5px, $break-roulette-lg);
    @include customIcon2(5 * 9px, 5px, $break-main-800, false);
    @include customIcon2(5 * 8px, 4px, $break-main-600);
    @include customIcon2(5 * 6px, 3px, $break-main-400);

    @media screen and (max-width: $break-main-600) {
      border-radius: 4px;
    }
  }

  &-7 {
    @include customIcon2(5 * 7px, 4px);
    @include customIcon2(5 * 6px, 3px, $break-main-800);
    @include customIcon2(5 * 5px, 2px, $break-main-600);

    @media screen and (max-width: $break-main-800) {
      border-radius: 4px;
    }
  }

  &-7-top {
    @include customIcon2(5 * 7px, 4px);
    @include customIcon2(5 * 6px, 3px, 1300px);
    @include customIcon2(5 * 5px, 2px, 1120px);
  }

  &-6 {
    @include customIcon2(5 * 6px, 3px);
    @include customIcon2(5 * 5px, 2px, $break-main-800);
  }

  &-5 {
    @include customIcon2(5 * 5px, 2px);
  }
}
