@font-face {
  font-family: #{$icomoon-font-family};
  src: url("#{$icomoon-font-path}/#{$icomoon-font-family}.woff") format("woff"), url("#{$icomoon-font-path}/#{$icomoon-font-family}.ttf") format("truetype"), url("#{$icomoon-font-path}/#{$icomoon-font-family}.svg##{$icomoon-font-family}") format("svg");
  font-weight: normal;
  font-style: normal;
}


%im, [class^="icomoon-"]:before, [class*=" icomoon-"]:before {
  // use !important to prevent issues with browser extensions that change fonts
  font-family: #{$icomoon-font-family} !important;
  speak: none;
  //font-size: 1rem
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  // Better Font Rendering ===========
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icomoon {
  &-tron {
    &:before {
      content: $icomoon-tron;
    }
